<template>
  <div>
      <div class="row">
        <div class="col-md-12">
          <button class="btn btm-sm btn-success pull-right" data-coreui-toggle="modal" data-coreui-target="#referralModal">New Referrals</button>
        </div>
      </div>
      <br>
    <vue-good-table
      :columns="columns"
      :rows="rows"
      :pagination-options="{
        enabled: true,
        records: 'pages',
      }"/>

      <div class="modal fade" id="referralModal" data-coreui-backdrop="static" data-coreui-keyboard="false" tabindex="-1" aria-labelledby="staticBackdropLiveLabel" aria-hidden="true">
        <div class="modal-dialog">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title" id="staticBackdropLiveLabel">Referral</h5>
              <button class="btn-close" type="button" data-coreui-dismiss="modal" aria-label="Close"></button>
            </div>
            <div class="modal-body">
              <form>
                <div class="row">
                  <div class="col-md-12">
                    <div class="form-group">
                      <label>Mobile</label>
                      <input type="text" class="form-control" v-model="form.mobile">
                    </div>
                  </div>
                </div>
              </form>
            </div>
            <div class="modal-footer">
              <button class="btn btn-secondary" type="button" data-coreui-dismiss="modal">Close</button>
              <button class="btn btn-primary" type="button" @click="submitForm">Save</button>
            </div>
          </div>
        </div>
      </div>
  </div>
</template>
<script>
import { VueGoodTable } from 'vue-good-table'
// import ConfirmDeleteButton from '../components/ConfirmDeleteButton.vue'
import { post } from '../helpers/api'
import swal from 'sweetalert';
export default {
  name:"Referrals",components:{ VueGoodTable },
    data(){
      return {
        columns: [
          {
            label: 'Mobile',
            field: 'strMobile',
          },
          {
            label: 'Referral Code',
            field: 'strReferralCode'
          },
          {
            label: 'Referral Status',
            field: 'strReferralStatus',
          },
          {
            label: 'Level',
            field: 'intLevel',
          },
          
          {
            label: 'Reward Amount',
            field: 'intRewardAmount',
          },
          {
            label: 'ROE',
            field: 'roe',
          },
          {
            label: 'Referred By',
            field: 'strReferredBy',
          },
          {
            label: 'User',
            field: 'strUser',
          },
        ],
        rows: [],
        form:{
          mobile:''
        }
      }
    },
    created(){
      this.loadData();
    },
    methods:{
      loadData () { 
        post('/referral/get_referral').then(res=>{
          this.rows = res.data.arrList;
        })
      },
      async submitForm(){
        await post('referral/create_referral',{strMobile:this.form.mobile}).then(()=>{
            swal("Success!", "New Referral Created", "success");
            this.form.mobile = "";
            this.loadData();
        });
      }

    }
}
</script>
