<template>
    <div>
      <div class="row">
        <div class="col-md-12">
          <button class="btn btm-sm btn-success pull-right" data-coreui-toggle="modal" data-coreui-target="#levelModal">New Level</button>
        </div>
      </div>
      <br>
    <vue-good-table
      :columns="columns"
      :rows="rows"
      :pagination-options="{
        enabled: true,
        records: 'pages',
      }">
      <template slot="table-row" slot-scope="props">
        <div class="action-div" v-if="props.column.field =='action'">
          <router-link v-bind:to="{name:'admin_course_level_videos',params:{course_id:course_id,level_id:props.row.id}}"><span class="fa fa-video"></span></router-link>
          <router-link v-bind:to="{name:'admin_course_questions',params:{course_id:course_id,level_id:props.row.id}}"><span class="fa fa-question-circle"></span></router-link>
          <ConfirmDeleteButton class="del-button" v-if="props.column.field =='action'" url="course/delete_level" title="Do want to delete?" v-bind:data="{'id':props.row.id }" v-bind:callback="loadData"></ConfirmDeleteButton>
        </div>
      </template>
    </vue-good-table>
      <div class="modal fade" id="levelModal" data-coreui-backdrop="static" data-coreui-keyboard="false" tabindex="-1" aria-labelledby="staticBackdropLiveLabel" aria-hidden="true">
        <div class="modal-dialog">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title" id="staticBackdropLiveLabel">Level</h5>
              <button class="btn-close" type="button" data-coreui-dismiss="modal" aria-label="Close"></button>
            </div>
            <div class="modal-body">
              <form>
                <div class="row">
                  <div class="col-md-12">
                    <div class="form-group">
                      <label>Level Name</label>
                      <input type="text" class="form-control" v-model="form.level_name">
                    </div>
                  </div>
                </div>
                  <div class="row">
                  <div class="col-md-12">
                    <div class="form-group">
                      <label>Level Order</label>
                      <input type="text" class="form-control" v-model="form.level_order">
                    </div>
                  </div>
                </div>
              </form>
            </div>
            <div class="modal-footer">
              <button class="btn btn-secondary" type="button" data-coreui-dismiss="modal">Close</button>
              <button class="btn btn-primary" type="button" @click="submitForm">Save</button>
            </div>
          </div>
        </div>
      </div>
  </div>
</template>
<style scoped>
  span.fa{
    margin: 5px;
    cursor: pointer;
  }
  .action-div{
    text-align: right;
  }
  .del-button {display: inline; margin-left: 15px; }
</style>
<script>
import { VueGoodTable } from 'vue-good-table'
import ConfirmDeleteButton from '../components/ConfirmDeleteButton.vue'
import { get,post } from '../helpers/api'
import swal from 'sweetalert';
export default {
    name:"CourseLevels",
    components:{VueGoodTable,ConfirmDeleteButton},
     data(){
      return {
        columns: [
          {
            label: 'Name',
            field: 'strName',
          },
          {
            label: 'Order',
            field: 'intLevelOrder'
          },
          {
            label: 'Created Time',
            field: 'createdTime',
          },
          
          {
            label : 'Action',
            field : 'action'
          }
        ],
        rows: [],
        course_id:"",
        form: {
          level_name:"",
          level_order:""
        }
      }
    },
    created(){
      this.loadData();
      this.course_id = this.$route.params.id
    },
    methods:{
      loadData () { 
        get('course/get_course').then(res=>{
          this.rows = res.data.arrLevels;
        })
      },

      rowClick(){
        //swal("Success!", "New Referral Created", "success");
      },
      async submitForm(){
        await post('course/create_level',{strName:this.form.level_name,fkCourseId:this.course_id,intLevelOrder:this.form.level_order,}).then(()=>{
            swal("Success!", "New Level Created", "success");
            this.form.level_name = "";
            this.form.level_order = "";
            this.loadData();
        });
       }

    }
}
</script>