<template>
  <div>
      <div class="row">
        <div class="col-md-12">
          <button class="btn btm-sm btn-success pull-right" data-coreui-toggle="modal" data-coreui-target="#referralModal">New News</button>
        </div>
      </div>
      <br>
    <vue-good-table
      :columns="columns"
      :rows="rows"
      :pagination-options="{
        enabled: true,
        records: 'pages',
      }">
      <template slot="table-row" slot-scope="props">
        <div class="action-div" v-if="props.column.field =='action'">
          <ConfirmDeleteButton class="del-button" v-if="props.column.field =='action'" url="news/delete_news" title="Do want to delete?" v-bind:data="{'id':props.row.id }" v-bind:callback="loadData"></ConfirmDeleteButton>
        </div>
      </template>
    </vue-good-table>

      <div class="modal fade" id="referralModal" data-coreui-backdrop="static" data-coreui-keyboard="false" tabindex="-1" aria-labelledby="staticBackdropLiveLabel" aria-hidden="true">
        <div class="modal-dialog">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title" id="staticBackdropLiveLabel">News</h5>
              <button class="btn-close" type="button" data-coreui-dismiss="modal" aria-label="Close"></button>
            </div>
            <div class="modal-body">
              <form>
                <div class="row">
                  <div class="col-md-12">
                    <div class="form-group">
                      <label>Title</label>
                      <input type="text" class="form-control" v-model="form.title">
                    </div>
                  </div>
                </div>
                 <div class="row">
                  <div class="col-md-12">
                    <div class="form-group">
                      <label>Description</label>
                      <input type="text" class="form-control" v-model="form.description">
                    </div>
                  </div>
                </div>
              </form>
            </div>
            <div class="modal-footer">
              <button class="btn btn-secondary" type="button" data-coreui-dismiss="modal">Close</button>
              <button class="btn btn-primary" type="button" @click="submitForm">Save</button>
            </div>
          </div>
        </div>
      </div>
  </div>
</template>

<script>
import { VueGoodTable } from 'vue-good-table'
import { post } from '../helpers/api'
import ConfirmDeleteButton from '../components/ConfirmDeleteButton.vue'
import swal from 'sweetalert';
export default {
  name:"News",
  components : { VueGoodTable,ConfirmDeleteButton },
  data(){
      return {
        columns: [
          {
            label: 'Title',
            field: 'jsnDetails.strHead',
          },
          {
            label: 'Type',
            field: 'chrType'
          },
          {
            label : 'Action',
            field : 'action'
          }
        ],
        rows: [],
        form:{
          title:"",
          description:"",
          meadia_type:"IMAGE",
          url:""
        }
      }
    },
  created(){
    this.loadData();
  },
  methods:{
    loadData () { 
        post('news/get_news').then(res=>{
          this.rows = res.data.arrList;
        })
      },
      async submitForm(){
        await post('news/create_news',{jsnDetails:{strHead:this.form.title,strDiscription:this.form.description,strUrl:this.form.url,strMeadiaType:this.form.meadia_type}}).then(()=>{
            swal("Success!", "New News Created", "success");
            this.form.title = this.form.description = this.form.url = "";
            this.loadData();
        });
       }
  }
}
</script>
